.App {
  text-align: center;
  background-color: beige;

  font-family:'Courier New', Courier, monospace;
}

main {
  height: 800px;
  width: 800px;
  background-color: beige;
  grid-template-columns: 1000px;
  margin: auto;
}

.game-window {
  height: 600px;
  width: 400px;
  background-color: yellow;
  border: 5px grey solid;
  display: grid;
  grid-template-columns: 1fr;
  text-align: center;
  margin: auto;
  border-radius: 15px;
  background-image: url("https://thumbs.dreamstime.com/b/empty-living-room-interior-concept-fashion-home-lounge-stairs-big-sofa-furniture-decorations-good-cosy-place-relax-179348883.jpg");
  background-size: cover;
  background-position: center;
  position: relative;
}

.top-nav {
  width: 400px;
  height: 100px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  text-align: center;
  margin: auto;
}


.nav-button-status {

  display: grid;
  grid-template-columns: 1fr;

}


.nav-button-status div{


  display: grid;
  grid-template-columns: 25% 75%;

}


.nav-button-status img{
  width: 25px;
  height: auto;
  margin: auto;

}

.nav-button-status p{
  width: 75px;
  height: auto;
  margin: auto;
  font-size: 12px;
}

.hunger-heart {
  width: 50px;
  height: auto;

}

.game-content {
  height: 410px;
  text-align: center;
  margin: auto;
}

.game-content img{
  height: 200px;
  margin: auto;
  margin-top: 200px;
}


.nav-grid {
  width: 400px;
  height: 90px;
  display: grid;
  grid-template-columns: repeat(5, 80px);
  background-color: #F6E47B;
  border-radius: 10px;

}

.nav-button {
  border: 4px #f6bb6f solid;
  width: 50px;
  height: 50px;
  border-radius: 10px;
  margin: auto;
  background-color: beige;
  background-size: cover;
  background-position: center;

}

.nav-button:hover {
  transform: scale(105%);
}


.nav-button.settings {

  background-image: url(https://www.iconpacks.net/icons/2/free-settings-icon-3110-thumb.png);
}

.nav-button.one {
  background-image: url(https://w7.pngwing.com/pngs/964/816/png-transparent-chocolate-chip-cookie-fortune-cookie-biscuits-cookie-clicker-cookies-cartoon-food-baking-cooking.png);

}

.nav-button.two {
  background-image: url(https://upload.wikimedia.org/wikipedia/en/thumb/e/e0/WPVG_icon_2016.svg/1200px-WPVG_icon_2016.svg.png);
}

.nav-button.four {
  background-image: url(https://icons.iconarchive.com/icons/paomedia/small-n-flat/1024/shop-icon.png);
}


.nav-button.five {
  background-image: url(https://www.iconpacks.net/icons/2/free-settings-icon-3110-thumb.png);
}


.black-overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-color: rgba(00, 00, 00, .7);
}

.treat-container {
  height: 75%;
  width: 75%;
  background-color: #f6bb6f;
  margin: auto;
  text-align: center;
  border: 5px solid #af5923;
  margin-top: 10%;
}

.treat-blocks {
  display: grid;
  grid-template-columns: 1fr;
}

.treat-blocks h3 {
  
  color: #7d1d17;
}

.exitButton {
  color: beige;
  background-color: grey;
  width: 25px;
  height: 25px;
  top: 0;
  left: 0;
}

.treatBlock {
  display: grid;
  grid-template-columns: 20% 60% 20%;
  width: 80%;
  height: 50px;
  background-color: #eac581;
  margin: auto;
  align-items: center;
  border: 2px solid #af5923;
  border-radius: 1px;
}

.treatBlock:hover {
  
  background-color: #e2ad5f;
  /* transform: scale(105%); */
}

.treatBlock h4, p{
  text-align: left;
  margin-bottom: 0px;
  margin-top: 0px;
  color: #5d1e16;

} 
.treatBlock h4{
  font-size: 8px;
  font-weight: bold;
} 
.treatBlock p{
  font-size: 6px;
}

.treatBlock img{
  width: 50%;
  margin: auto;
}

#treat {
  position: absolute;
  top: 0px;
  left: 0px;
}

#treat img{
  height: 20px;
  width: 20px;
  top: 0px;
  left: 0px;
}

.prompt-box {
  position: absolute;
}